.nav-wrapper {
  border-bottom: 1px solid #e2e2e2;
}

.sidebar-wrapper {
  height: 100%;
  width: 250px;
  overflow: scroll;
  position: fixed;
  top: 0;
  z-index: 10;
  box-shadow: 0 8px 13px -6px #00000085;
  background-color: white;
  padding: 0.75rem;
  display: flex;

  .sidebar-list {
    min-width: 12.5rem;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &__item {
      display: flex;
      &--text {
        padding: 0.5rem;
        flex-basis: 100%;
        font-weight: bolder;
        color: black;
        text-decoration: none;
        font-size: 0.875rem;
      }
      :hover {
        background-color: rgba(77, 188, 241, 0.1);
      }
    }
  }
}

.fa-times {
  position: fixed;
  right: 0;
  margin-right: 1rem;
  z-index: 10;
}

@media (max-width: 575.98px) {
  .sidebar-wrapper {
    width: 100%;

    .sidebar-list__item--text {
      padding: 1.5rem 0;
    }

    .list-group {
      width: 100%;
      text-align: center;
    }
  }
}
